import { Router, Request, Response } from "express";
import { ProfileController } from "../controllers/Profiles/profileController.ts";
import { upload } from "../middlewares/upload.ts";
import { formatFileSize } from "../utils/formatFileSize.ts";
import { compressImage } from "../utils/compressImage.ts";

const router = Router();

declare global {
  namespace Express {
    interface Request {
      user: { id: string };
    }
  }
}

router.post("/", async (req: Request, res: Response): Promise<void> => {
  const { nickname, bio, picture, links } = req.body;

  const userId: string = (req.user as { id: string }).id;

  const result = await ProfileController.createProfileController({
    nickname,
    bio,
    picture,
    links,
    reqUserId: userId,
  });

  if (result.error) {
    res.status(400).json({ error: result.error });
    return;
  }

  res.status(201).json(result);
});

router.get("/myprofile", async (req: Request, res: Response): Promise<void> => {
  const userId: string = (req.user as { id: string }).id;

  const result = await ProfileController.myProfileController({
    reqUserId: userId,
  });

  if (result.error) {
    res.status(400).json({ error: result.error });
    return;
  }

  res.status(200).json(result);
});


router.post("/find", async (req: Request, res: Response): Promise<void> => {
  const userId: string = (req.user as { id: string }).id;

  const result = await ProfileController.findProfilesByNicknameController({
    reqUserId: userId,
    nickname: req.body.nickname
  });

  if (result.error) {
    res.status(400).json({ error: result.error });
    return;
  }

  res.status(200).json(result);
});


router.post("/upload", upload.single("picture"),async (req: Request, res: Response): Promise<void> => {
  try {
    const file = req.file

    if (!file) {
      res.status(400).json({ error: "No files received."})
      return
    }

    const compress: string[] = await compressImage(file.filename, file.path) 
 
    console.log("compress: ", compress)
  
    const objImage: object = {
      fieldname: 'picture',
      originalname: file?.originalname,
      filename: file?.filename,
      path: file?.path,
      size: formatFileSize(Number(file?.size)),
      compress: compress
    }
  
    res.status(200).json({ sucess: objImage });
  } catch (error) {
    console.error('Error uploading and compressing the image:', error);
    res.status(500).json({ error: 'Error uploading and compressing the image' });
  }
});

export default router;
