import sharp from 'sharp';

const sizes: number[] = [150, 600, 1080]
const names: string[] = [];

export const compressImage = async (filename: string, path: string) => {

  sizes.forEach(async item => {
    const compressedImagePath = `${item}-compressed_${filename}`;

    await sharp(path)
      .resize({ width: item })
      .png({ quality: 80, })
      .toFile(`src/uploads/${compressedImagePath}`)
      .then(data => {
        console.log("data: ", data)
        names.push(compressedImagePath)
      })

  })

  return names
};
